html {
  height: 100%;
}
body {
  font-size: 18px;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(51, 54, 56);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

.App {
  width: 100%;
  height: 100%;
}

.App h1 {
  background: slategray;
  color: white;
  height: 80px;
  margin: auto;
}

#form {
  width: 70%;
  height: inherit;
  margin: 20px auto;
  padding-top: 10px;
  padding-bottom: 50px;
  background: slategray;
  color: white;
}

#request-name {
  margin: 20px;
}

#request-name input {
  font-size: 18px;
  margin: 10px auto;
  width: 70%;
}

#time-slider {
  margin-top: 40px;
  width: 100%
}

#duration-label {
  padding-left: 10px;
}

#slider {
  position: relative;
  top: 2px;
  left: 5px;
  -webkit-appearance: none;
  width: 50%;
  height: 15px;
  border-radius: 5px;
  background: white;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}

#slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #000000;
  cursor: pointer;
}

#slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #000000;
  cursor: pointer;
}

/* Spec Selection Button */
#spec-selection {
  margin-top: 50px;
}

#spec-selection button {
  background: #d20e00;
  color: white;
  margin: 10px 30px;
  width: 250px;
  font-size: 20px;
  line-height: 1.5em;
}

#btn-quality.selected {
  background: green;
}

#btn-security.selected {
  background: green;
}

#btn-backup.selected {
  background: green;
}

/* Submit Button */

#submit-button {
  padding-top: 100px;
  margin: auto;
  width: 100%;
}

#submit-button button{
  width: 100%;
  font-size: 20px;
  color: white;
  background: #5bb3e6;
  line-height: 2em;
}

#submit-button button.btn-disabled{
  background: grey;
}

#redirect-button {
  background: limegreen !important;
}

#redirect-button.btn-cancel {
  background: #d20e00 !important;
}

#reset-button {
  background: #d20e00 !important;
  margin-top: 50px;
}

/* Post target */
#post-target {
  margin: 20px auto;
  width: 100%;
}

#post-target input {
  font-size: 20px;
  width: 90%;
  line-height: 1.5em;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

